<template>
	<div>
		<div class="headerbg">
			<vheader></vheader>
			<navs_header ref="navsheader"></navs_header>
		</div>
		<div class="contentbg">
			<!-- Begin-->
			 	<navs_left ref="navleft" v-on:childByValue="childByValue"></navs_left>	
			<!-- End-->
			<div class="wrapper wrapper-content">
				<div class="ibox-content">
					<!--<navs_button></navs_button>-->
					<div class="fixed-table-toolbar clearfix">
						<div class="bs-bars pull-left">
							<div class="hidden-xs" id="roleTableToolbar" role="group">
								<button type="button" class="btn btn-green" @click="openAddMgr" id="">
                                	<i class="icon-add"></i>
                      			 </button>
								<button type="button" class="btn btn-blue button-margin" @click="modify">
								    <i class="icon-edit"></i>
								</button>
								<button type="button" class="btn btn-red button-margin" @click="deluserinfo">
								    <i class="icon-trash"></i>
								</button>
								<button type="button" class="btn btn-primarys button-margin">
								    <i class="icon-inventory-line-callout"></i>
								</button>
								<button type="button" class="btn btn-primarys button-margin">
								    <i class="icon-send"></i>
								</button>
								<button type="button" class="btn btn-primarys button-margin">
								    <i class="icon-close1"></i>
								</button>
								<button type="button" class="btn btn-primarys button-margin" @click="modestsearch">
						    		<i class="icon-search"></i>
						    		<i class="icon-arrow1-down" v-show="down"></i>
						    		<i class="icon-arrow1-up" v-show="up"></i>
								</button>
							</div>
						</div>
						<div class="columns columns-right btn-group pull-right">
							<div id="refresh" title="" class="btn btn-default btn-refresh"><i class="icon-refresh"></i></div>
							<div class="keep-open btn-group" title="">
								<el-dropdown :hide-on-click="false" class="pl10 btn btn-default btn-outline">
									<span class="el-dropdown-link">
										<font class="J_tabClose"><i class="icon-menu3"></i></font>
										<i class="el-icon-arrow-down icon-arrow2-down"></i>
									</span>
									<el-dropdown-menu slot="dropdown">
										<el-checkbox-group v-model="checkedName" @change="test">
											<el-dropdown-item  v-for="(item,index) in tableHeader" :key="index">
												<el-checkbox :label="item.label" name="type"></el-checkbox>
											</el-dropdown-item>
										</el-checkbox-group>
									</el-dropdown-menu>
								</el-dropdown>
							</div>
						</div>
					</div>
					<!--  Begin-->
					<div v-show="search">
						<el-form inline-message :model="searchList" label-width="110px">
							<el-row :gutter="5">
								<el-col :span="6">
									<el-form-item label="" prop="WONUM">
										<el-input v-model="searchList.WONUM">
										</el-input>
									</el-form-item>
								</el-col>
								<el-col :span="6">
									<el-form-item label="" prop="ITEM_NAME" label-width="80px">
										<el-input v-model="searchList.ITEM_NAME">
										</el-input>
									</el-form-item>
								</el-col>
								<el-col :span="6">
									<el-form-item label="" prop="COMPLETE_DATE" label-width="80px">
										<div class="block">
									    	<el-date-picker v-model="searchList.COMPLETE_DATE" type="date" placeholder="" style="width: 100%" value-format="yyyy-MM-dd">
									    	</el-date-picker>
								  		</div>
									</el-form-item>
								</el-col>
								
								<!-- <el-col :span="3">
									<el-select v-model="searchList.STATE" placeholder="">
										<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
										</el-option>
									</el-select>
								</el-col> -->
							</el-row>
							<el-row :gutter="5">
								<el-col :span="6">
									<el-form-item label="" prop="PROXYNUM" label-width="110px">
										<el-input v-model="searchList.PROXYNUM">
										</el-input>
									</el-form-item>
								</el-col>
								<el-col :span="6">
									<el-form-item label="" prop="ENTERDATE" label-width="80px">
										<div class="block">
									    	<el-date-picker v-model="searchList.ENTERDATE" type="date" placeholder="" style="width: 100%" value-format="yyyy-MM-dd">
									    	</el-date-picker>
								  		</div>
									</el-form-item>
								</el-col>
                                    <el-col :span="4">
									<el-button type="primary" @click="searchinfo" size="small" style="margin-top:2px"></el-button>
									<el-button type="primary" @click="resetbtn" size="small" style="margin-top:2px;    margin-left: 2px"></el-button>
								</el-col>
							</el-row>
						</el-form>
					</div>
					<!--  End-->
					
					<el-row :gutter="10">
						<el-col :span="5" class="lefttree">
							<div class="lefttreebg">
								<div class="left_tree_title clearfix" @click="min3max()">
									<div class="pull-left pr20" v-if="ismin"></div>
									<span class="pull-right navbar-minimalize minimalize-styl-2">
										<i class="icon-doubleok icon-double-angle-left blue"></i>
									</span>
								</div>
								<div class="left_treebg" :style="{height: fullHeight}">
									<div class="p15" v-if="ismin">
										<el-tree ref="tree" class="filter-tree" :data="resourceData" node-key="id" default-expand-all :indent="22" :render-content="renderContent"  :props="resourceProps" @node-click="handleNodeClick">
										</el-tree>
									</div>
								</div>
							</div>
						</el-col>
						<el-col :span="19" class="leftcont v-resize">
							<!--  -->
							<el-table :header-cell-style="rowClass" 
									  :data="userList" 
									  border 
									  stripe 
									  :height="fullHeight" 
									  style="width: 100%;" 
									  :default-sort="{prop:'userList', order: 'descending'}" 
									  @selection-change="SelChange" 
									  v-loadmore="loadMore"
									  v-loading="loading"  
									  element-loading-text=""
    								  element-loading-spinner="el-icon-loading"
    								  element-loading-background="rgba(255, 255, 255, 0.9)">
								<el-table-column type="selection" width="55" fixed v-if="this.checkedName.length>0" align="center">
								</el-table-column>
								<el-table-column label="" sortable width="140px" prop="WONUM" v-if="this.checkedName.indexOf('')!=-1">
									<template slot-scope="scope">
									<p class="blue" title="" @click=view(scope.row.ID)>{{scope.row.WONUM}}
										</p>
									</template>	
								</el-table-column>
								<el-table-column label="" sortable width="180px" prop="ITEM_NAME" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column>
								<el-table-column label="" sortable width="180px" prop="ITEM_MODEL" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column>
								<el-table-column label="" sortable  width="100px" prop="ITEM_STATUS" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column>
								<el-table-column label="/" sortable width="200px" prop="CHECK_BASIS" v-if="this.checkedName.indexOf('/')!=-1">
								</el-table-column>
								<el-table-column label="" sortable  width="100px" :formatter="dateFormat" prop="COMPLETE_DATE" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column>
								<el-table-column label="" sortable  width="100px" prop="COMPLETE_MODE" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column>
								<el-table-column label="" sortable  width="120px" prop="PROXYNUM" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column>
								<!-- <el-table-column label="" sortable width="100px" prop="STATUS" :formatter="judge" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column> -->
								<!-- <el-table-column label="" sortable width="210px" prop="ENTERBY" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column> -->
								<el-table-column label="" sortable width="210px" :formatter="dateFormat" prop="ENTERDATE" v-if="this.checkedName.indexOf('')!=-1">
								</el-table-column>
							</el-table>
							<el-pagination background class="pull-right pt10" v-if="this.checkedName.length>0"
					            @size-change="sizeChange"
					            @current-change="currentChange"
					            :current-page="page.currentPage"
					            :page-sizes="[10, 20, 30, 40]"
					            :page-size="page.pageSize"
					            layout="total, sizes, prev, pager, next" :total="page.totalCount">
					        </el-pagination>
							<!--  -->
						</el-col>
					</el-row>
					</div>
				</div>
			</div>
			<workorders_mask :workorderForm="workorderForm" ref="child" @requests="requestData" @requestTree="getKey" v-bind:page=page></workorders_mask>
		</div>
	</div>
</template>
<script>
	import Config from '../../config.js'
	import vheader from '../common/vheader.vue'
	import navs_left from '../common/left_navs/nav_left5.vue'
	import navs_header from '../common/nav_tabs.vue'
	import workorders_mask from '../testworkcheckDetails/workorders_mask.vue'
	export default {
		name: 'workorders',
		components: {
			vheader,
			navs_header,
			navs_left,
			workorders_mask
		},
		data() {
			return {
				loading: false,
				basic_url: Config.dev_url,
				ismin: true,
				loadSign:true,//
				commentArr:{},
				fullHeight: document.documentElement.clientHeight - 210+'px',//
				checkedName: [
					'',
					'',
					'',
					'',
					'/',
					'',
					'',
					'',
					// '',
					'',
					// ''
				],
				tableHeader: [
					{
						label: '',
						prop: 'WONUM'
					},
					{
						label: '',
						prop: 'ITEM_NAME'
					},
					{
						label: '',
						prop: 'ITEM_MODEL'
					},
					{
						label: '',
						prop: 'ITEM_STATUS'
					},
					{
						label: '/',
						prop: 'CHECK_BASIS'
					},
					{
						label: '',
						prop: 'COMPLETE_DATE'
					},
					{
						label: '',
						prop: 'COMPLETE_MODE'
					},
					{
						label: '',
						prop: 'PROXYNUM'
					},
					// {
					// 	label: '',
					// 	prop: 'STATUS'
					// },
					// {
					// 	label: '',
					// 	prop: 'ENTERBY'
					// },
					{
						label: '',
						prop: 'ENTERDATE'
					}
				],
				companyId: '',
				deptId: '',
				selMenu: [],
				userList: [],
				search: false,
				show: false,
				down: true,
				up: false,
				options: [{
					value: '1',
					label: ''
				}, {
					value: '0',
					label: ''
				}],
				searchList: {
					WONUM: '',//
					ITEM_NAME: '',//
					PROXYNUM: '',//
					STATE: '',//
					COMPLETE_DATE: '',//
					// ENTERBY: '',//
					ENTERDATE: '',//
				},
				//tree
				resourceData: [], //
				resourceDialogisShow: false,
				resourceCheckedKey: [], // [1,3,15,18,...]
				resourceProps: {
					children: "subDepts",
					label: "fullname"
				},
				treeData: [],
				page: {
					currentPage: 1,
					pageSize: 10,
					totalCount: 0
				},
				workorderForm: {}//
			}
		},
		methods: {
			//
			rowClass({ row, rowIndex}) {
			    return 'text-align:center'
			},
			renderContent(h, {node,data,store}) { //Element
				return(<span><i class={data.iconClass}></i><span>{node.label}</span></span>);
			},
			// 
			nodeClick: function(m) {
				if(m.iconClass != 'icon-file-text') {
					if(m.iconClass == 'icon-file-normal') {
						m.iconClass = 'icon-file-open';
					} else {
						m.iconClass = 'icon-file-normal';
					}
				}
				this.handleNodeClick();
			},
			test(){
				console.log(this.checkedName.indexOf('')!=-1);
			},
			loadMore() {
				if(this.loadSign) {
					this.loadSign = false
					this.page.currentPage++
						if(this.page.currentPage > Math.ceil(this.page.totalCount / this.page.pageSize)) {
							return
						}
					setTimeout(() => {
						this.loadSign = true
					}, 1000)
					this.requestData()
					//console.log('', this.page.currentPage)
				}
			},
			sizeChange(val) {
		      this.page.pageSize = val;
		      this.requestData();
		    },
		    currentChange(val) {
		      this.page.currentPage = val;
		      this.requestData();
		    },
		    	resetbtn(){
				this.searchList =  { //
					WONUM: '',//
					ITEM_NAME: '',//
					PROXYNUM: '',//
					STATE: '',//
					COMPLETE_DATE: '',//
					// ENTERBY: '',//
					ENTERDATE: '',//
				};
				this.requestData();
			},
			searchinfo(index) {
				this.page.currentPage = 1;
				this.page.pageSize = 10;
				this.requestData();
			},
			//
			openAddMgr() {
				
				this.$refs.child.visible();
			},
			//
			modify() {
				if(this.selMenu.length == 0) {
					this.$message({
						message: '',
						type: 'warning'
					});
					return;
				} else if(this.selMenu.length > 1) {
					this.$message({
						message: '',
						type: 'warning'
					});
					return;
				} else {
					if(this.selMenu[0].STATE == 3 || this.selMenu[0].STATE == 2) {
						this.$message({
							message: '',
							type: 'warning'
						});
						this.$refs.child.view(this.selMenu[0].ID);
					}
					//
					else if(this.selMenu[0].STATE == 0) {
						var url = this.basic_url + '/api-apps/app/workorder/flow/isExecute/' + this.selMenu[0].ID;
						this.$axios.get(url, {}).then((res) => {
							if(res.data.resp_code == 0) {
								var url = this.basic_url + '/api-apps/app/workorder/flow/isPromoterNode/' + this.selMenu[0].ID;
								this.$axios.get(url, {}).then((res) => {
									if(res.data.resp_code == 0) {
										this.$refs.child.detail(this.selMenu[0].ID);
									} else {
										this.$message({
											message: res.data.resp_msg,
											type: 'warning'
										});
									}
								});
							} else {
								this.$message({
									message: res.data.resp_msg,
									type: 'warning'
									});
							}
						});
					}else{
						this.$refs.child.detail(this.selMenu[0].ID);	
					}
				}
			},
			//
			view(id) {
				this.$refs.child.view(id);
			},
			//
			getRouterData() {
				// query
				this.id = this.$route.query.bizId;
				this.$refs.child.view(this.id);
			},
			//
			modestsearch() {
				this.search = !this.search;
				this.down = !this.down,
				this.up = !this.up
			},
			// 
			deluserinfo() {
				var selData = this.selMenu;
				if(selData.length == 0) {
					this.$message({
						message: '',
						type: 'warning'
					});
					return;
				}else {
					var url = this.basic_url + '/api-apps/app/workorder/deletes';
					//changeUser
					var changeUser = selData;
					//deleteidid
					var deleteid = [];
					var ids;
					for (var i = 0; i < changeUser.length; i++) {
						if(changeUser[i].STATE!=1){
						 	this.$message({
								message: '',
								type: 'error'
							});
							return;
						}else{
							deleteid.push(changeUser[i].ID);
						}
					}
					//idsdeleteid
					ids = deleteid.toString(',');
                    var data = {
						ids: ids,
					}
					this.$confirm('', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                    }).then(({ value }) => {
                        this.$axios.delete(url, {params: data}).then((res) => {
							if(res.data.resp_code == 0) {
								this.$message({
									message: '',
									type: 'success'
								});
								this.requestData();
							}
						}).catch((err) => {
							this.$message({
								message: '',
								type: 'error'
							});
						});
                    }).catch(() => {

                	});
				}
			},
			judge(data) {
				//taxStatus 
				return data.enabled ? '' : ''
			},
			
			//  
			dateFormat(row, column) {
				var date = row[column.property];
				if(date == undefined) {
					return "";
				}
				return this.$moment(date).format("YYYY-MM-DD");
				// return this.$moment(date).format("YYYY-MM-DD HH:mm:ss");  
			},
			
			SelChange(val) {
				this.selMenu = val;
			},
			requestData() {
				this.loading = true;
				var data = {
					page: this.page.currentPage,
					limit: this.page.pageSize,

					WONUM: this.searchList.WONUM,
					ITEM_NAME: this.searchList.ITEM_NAME,
					PROXYNUM: this.searchList.PROXYNUM,
					STATE: this.searchList.STATE,
					COMPLETE_DATE: this.searchList.COMPLETE_DATE,
					ENTERBY: this.searchList.ENTERBY,
					ENTERDATE: this.searchList.ENTERDATE,
				}
				var url = this.basic_url + '/api-apps/app/workorder';
				this.$axios.get(url, {
					params: data
				}).then((res) => {
					this.page.totalCount = res.data.count;	
					//
					this.userList=res.data.data;
					let totalPage=Math.ceil(this.page.totalCount/this.page.pageSize)
					if(this.page.currentPage >= totalPage){
						 this.loadSign = false
					}else{
						this.loadSign=true
					}
					this.commentArr[this.page.currentPage]=res.data.data
					let newarr=[]
					for(var i = 1; i <= totalPage; i++){
					
						if(typeof(this.commentArr[i])!='undefined' && this.commentArr[i].length>0){
							
							for(var j = 0; j < this.commentArr[i].length; j++){
								newarr.push(this.commentArr[i][j])
							}
						}
					}
					this.userList = newarr;
					this.loading = false;
				}).catch((wrong) => {})
			},
			//
			getKey() {
				var url = this.basic_url + '/api-user/depts/tree';
				this.$axios.get(url, {}).then((res) => {
					this.resourceData = res.data;
					this.treeData = this.transformTree(this.resourceData);
				});
			},
			transformTree(data){
				for(var i=0; i<data.length; i++){
					data[i].name = data[i].fullname;
					if(!data[i].pid || $.isArray(data[i].subDepts)){
						data[i].iconClass = 'icon-file-normal';
					}else{
						data[i].iconClass = 'icon-file-text';
					}
					if($.isArray(data[i].subDepts)){
						data[i].children = this.transformTree(data[i].subDepts);
					}
				}
				return data;
			},
			getTreeId(data){
				if(data.type == '1'){
					this.companyId = data.id;
					this.deptId = '';
				}else{
					this.deptId = data.id;
					this.companyId = '';
				}
				this.requestData();
			},
			handleNodeClick(data) {
				if(data.type == '1') {
					this.companyId = data.id;
					this.deptId = '';
				} else {
					this.deptId = data.id;
					this.companyId = '';
				}
				this.requestData();
			},
			formatter(row, column) {
				return row.enabled;
			},
			min3max() { //
				if($(".lefttree").hasClass("el-col-5")) {
					$(".lefttree").removeClass("el-col-5");
					$(".lefttree").addClass("el-col-1");
					$(".leftcont").removeClass("el-col-19");
					$(".leftcont").addClass("el-col-23");
					$(".icon-doubleok").removeClass("icon-double-angle-left");
					$(".icon-doubleok").addClass("icon-double-angle-right");
				} else {
					$(".lefttree").removeClass("el-col-1");
					$(".lefttree").addClass("el-col-5");
					$(".leftcont").removeClass("el-col-23");
					$(".leftcont").addClass("el-col-19");
					$(".icon-doubleok").removeClass("icon-double-angle-right");
					$(".icon-doubleok").addClass("icon-double-angle-left");
				}
				this.ismin = !this.ismin;
			},
			childByValue:function(childValue) {
        		// childValue
        		this.$refs.navsheader.showClick(childValue);
      		},
		},
		mounted() {
			this.requestData();
			this.getKey();
			if(this.$route.query.bizId != undefined) {
				this.getRouterData();
			}
			
		},
	}
</script>

<style scoped>

</style>


