<template>
	<div>
		<el-dialog :modal-append-to-body="false" title="" :visible.sync="dialoginspect" width="80%" >
			<el-form :model="searchList">
				<el-row :gutter="5">
					<el-col :span="6">
						<el-form-item label="" prop="V_NAME"  label-width="100px">
							<el-input v-model="searchList.V_NAME"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="5">
						<el-form-item label="" prop="ITEM_NAME" label-width="70px">
							<el-input v-model="searchList.ITEM_NAME"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="5">
						<el-form-item label="" prop="REPORT_NUM" label-width="100px">
							<el-input v-model="searchList.REPORT_NUM"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="5">
						<el-form-item label="" prop="PROXYNUM" label-width="110px">
							<el-input v-model="searchList.PROXYNUM"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row :gutter="5">
					<el-col :span="6">
						<el-form-item label="" prop="COMPDATE" label-width="100px">
							<el-date-picker v-model="searchList.COMPDATE" type="date" placeholder="" value-format="yyyy-MM-dd HH:mm:ss" style="width: 100%">
						</el-date-picker>
						</el-form-item>
					</el-col>
                    <el-col :span="4">
					<el-button type="primary" @click="searchinfo" size="small" style="margin-top:2px"></el-button>
					<el-button type="primary" @click="resetbtn" size="small" style="margin-top:2px;    margin-left: 2px"></el-button>
				</el-col>
				</el-row>
			</el-form>
			<el-table :data="inspectList" border stripe :header-cell-style="rowClass"  style="width: 100%;"height="400px" :default-sort="{prop:'inspectList', order: 'descending'}" @selection-change="SelChange" v-loadmore="loadMore('inspect')">	
				<el-table-column type="selection" width="55" fixed align="center">
				</el-table-column>
				<el-table-column label="" sortable width="130px" prop="PROXYNUM">
				</el-table-column>
				<el-table-column label="" sortable width="140px" prop="V_NAME">
				</el-table-column>
				<el-table-column label="" sortable width="140px" prop="P_NAME">
				</el-table-column>
				<el-table-column label="" sortable width="140px" prop="ITEM_NAME">
				</el-table-column>
				<el-table-column label="" sortable width="140px" prop="ITEM_MODEL">
				</el-table-column>
				<el-table-column label="" width="200px" prop="REMARKS" sortable>
				</el-table-column>
				<el-table-column label="" width="140px" prop="COMPDATE" sortable  :formatter="dateFormat" data-type = "">
				</el-table-column>
				<el-table-column label="" width="100px" prop="COMPMODE" sortable>
				</el-table-column>
				<el-table-column label="" width="140px" prop="REPORT_NUM" sortable>
				</el-table-column>
				<el-table-column label="" width="140px" prop="MAINGROUP" sortable>
				</el-table-column>
				<el-table-column label="" width="140px" prop="ENTERDATE" sortable :formatter="dateFormat">
				</el-table-column>
				<el-table-column label="" width="80" prop="VERSION" sortable>
				</el-table-column>
			</el-table>	
			<el-pagination background class="pull-right pt10" @size-change="sizeChange" @current-change="currentChange" :current-page="page.currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="page.pageSize" layout="total, sizes, prev, pager, next" :total="page.totalCount">
			</el-pagination>
			<span slot="footer" class="dialog-footer">
			   <el-button type="primary" @click="determine"> </el-button>
		       <el-button @click="close"> </el-button>
		    </span>
		</el-dialog>
	</div>
</template>

<script>
	import Config from '../../../config.js';
	export default {
  name: 'sample',
  
  data() {
    return {
		basic_url: Config.dev_url,
		inspectList: [],
		dialoginspect: false,
		loadSign:true,//
		commentArr:{},
		selval: [],//
		page: {
			currentPage: 1,
			pageSize: 20,
			totalCount: 0
			},
		searchList: {
				V_NAME:'',
				ITEM_NAME: '',
				REPORT_NUM: '',
				PROXYNUM: '',
				COMPDATE: '',
			},
    }
  },

  methods: {
  	dateFormat(row, column) {
		var date = row[column.property];
		if(date == undefined) {
			return "";
		}
		return this.$moment(date).format("YYYY-MM-DD");
	},
  	//
	rowClass({ row, rowIndex}) {
	    return 'text-align:center'
	},
	SelChange(val) {
		this.selval = val;
	},
  	sizeChange(val) {
		this.page.pageSize = val;
		this.requestData();
	},
	currentChange(val) {
		this.page.currentPage = val;
		this.requestData();
	},
	resetbtn(){
		this.searchList =  { //
			V_NAME:'',
			ITEM_NAME: '',
			REPORT_NUM: '',
			PROXYNUM: '',
			COMPDATE: '',
		};
	},
	searchinfo() {
		this.page.currentPage = 1;
		this.page.pageSize = 20;
		this.requestData();
	},
  	//
	close() {
		this.dialoginspect = false;
	},
  	visible() {
		this.dialoginspect = true;
  	},
  	loadMore () {
	   if (this.loadSign) {
	     this.loadSign = false
	     this.page.currentPage++
	     if (this.page.currentPage > Math.ceil(this.page.totalCount/this.page.pageSize)) {
	       return
	     }
	     setTimeout(() => {
	       this.loadSign = true
	     }, 1000)
	     this.requestData();
	   }
	},
	requestData(){
		var data = {
				page: this.page.currentPage,
				limit: this.page.pageSize,
				V_NAME:this.searchList.V_NAME,
				ITEM_NAME: this.searchList.ITEM_NAME,
				REPORT_NUM: this.searchList.REPORT_NUM,
				PROXYNUM: this.searchList.PROXYNUM,
				COMPDATE: this.searchList.COMPDATE,
			}
			//
			this.$axios.get(this.basic_url + '/api-apps/app/inspectPro', {params: data}).then((res) => {
				this.page.totalCount = res.data.count;
				//
				let totalPage = Math.ceil(this.page.totalCount / this.page.pageSize);
				if(this.page.currentPage >= totalPage) {
					this.loadSign = false
				} else {
					this.loadSign = true
				}
				this.commentArr[this.page.currentPage] = res.data.data
				let newarr = []
				for(var i = 1; i <= totalPage; i++) {
					if(typeof(this.commentArr[i]) != 'undefined' && this.commentArr[i].length > 0) {

						for(var j = 0; j < this.commentArr[i].length; j++) {
							newarr.push(this.commentArr[i][j])
						}
					}
				}
				this.inspectList = newarr;
			}).catch((wrong) => {
				this.$message({
							message: '',
							type: 'error'
						});
			})
	},
	determine(){
		if(this.selval.length == 0){
			this.$message({
				message: '',
				type: 'warning'
			});
		}else if(this.selval.length > 1){
			this.$message({
				message: '',
				type: 'warning'
			});
		}else{
			this.dialoginspect = false;
			var proxynum=this.selval[0].PROXYNUM;	
			var version=this.selval[0].VERSION;
			this.$emit('appendpro',proxynum);
			this.$emit('appendver',version);
			this.requestData();
		}
	},
  },
  mounted() {
			this.requestData();
		},
}
</script>

<style>
</style>


